export default {
  computed: {
    processes() {
      return this.$store.getters.method_preview
    }
  },
  watch: {
    processes(newValue) {
      console.log(newValue)
      newValue.map((value, index) => {
        value.order = `md:order-${(value.id % 2) + 1}`;
        console.log(value);
      });
    },
  },
};
